[class^="c-btn"] {
  height: 80px;
  width: calc(100% / 4 - 12px);
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  border-radius: 10px;
  padding: 0 16px;
  margin: 6px;
  min-width: 240px;
  &:nth-child(n + 5) {
    margin-top: 12px;
  }
  span {
    width: 32px;
    height: 32px;
  }
}
.c-btn-wrap {
  margin: 0 -6px;
  display: flex;
  flex-wrap: wrap;
}
.c-btn--gray {
  background-color: #f5f5f5;
}
