.l-header {
  &.is-show {
    .c-fixed-logo {
      left: 20px;
    }
    .c-navigation {
      top: 20px;
    }
  }
  [class^="u-icon-"],
  [class*="u-icon-"] {
    margin-right: 4px;
    &:before {
      color: #888888;
      font-size: 32px;
    }
  }
}
.l-header-nav {
  display: flex;
  height: 70px;
}
.l-header__logo {
  width: 250px;
  background: $color_theme;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  img {
    width: 210px;
  }
}
.l-header__menu {
  display: flex;
  width: 100%;
}
.l-header__menu__item {
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  color: $font-black;
}
.l-header__submenu {
  display: flex;
  width: 250px;
  flex-shrink: 0;
}
.l-header__submenu__item {
  width: calc(100% / 3);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 10px;
  transform: scale(0.9);
  color: $font-black;
}
